			const src = `${__imgPath}emotion/output/${emotion.filename}`;
			const IMAGE = `<img class="t-emotion" src="${src}" alt="${src}" />`;
			tinyMCE.editors["tinymce"].insertContent(IMAGE);
		},
	},
	mounted: function () {},
	components: {
		Editor,
		Upload,
		Resource,
		Emotion,
		BoxResource,
